import { useEffect, useState } from "react";
import { Route, Switch } from "react-router-dom";
import Index from "../pages/Index";
import Show from "../pages/Show";
import InputForm from "../pages/InputForm";




function Main(props) {
  const [cardList, setcardList] = useState(null);

  const URL = "https://warm-plateau-64054.herokuapp.com/cardList/";

  const getcardList = async () => {
    const response = await fetch(URL);
    const data = await response.json();
    setcardList(data);
  };

  console.log(cardList);

   const createcardList = async (card) => {
    // make post request to create cardList
    await fetch(URL, {
      method: "POST",
      headers: {"Content-Type": "Application/json",
      },
      body: JSON.stringify(card),
    });
    // update list of cardList
    setcardList();
   console.log(cardList);
 };

    const updatecardList = async (card, id) => {
    // make put request to create cardList
    await fetch(URL + id, {
      method: "PUT",
      headers: {
        "Content-Type": "Application/json",
      },
      body: JSON.stringify(card),
    });
    // update list of cardList
    getcardList();
  };

  const deletecardList = async (id) => {
    // make delete request to create cardList
    await fetch(URL + id, {
      method: "DELETE",
    });
    // update list of cardList

    getcardList();
  };

  useEffect(() => getcardList(), []);
  
  return (
    <main>
      <Switch>
        <Route exact path="/">
          <InputForm cardList={cardList} createcardList={createcardList}/>
          <Index cardList={cardList} createcardList={createcardList} />
          </Route>
        <Route 
          path="/cardlist/:id" 
          render={(rp) => (
            <Show   
              cardList={cardList}
              updatecardList={updatecardList}
              deletecardList={deletecardList}
              {...rp} 
            />
          )}
        />
      </Switch>
    </main>
  );
}

export default Main;
